export default {
  props: {
    item: {
      type: Object,
      default: null,
    },
  },
  data() {
    return {
      menu: false,
      isSelectDate: true,
      allowed: {
        dates: (d) => {
          return new Date(d).getDay() > 0 && new Date(d).getDay() < 6
        },
        hours: (h) => {
          return (
            h >= 7 &&
            h <= 19 &&
            (this.date === this.today.toISOString().substr(0, 10)
              ? h >= this.today.getHours()
              : true)
          )
        },
        minutes: (m) => {
          return m === 0 || m === 15 || m === 30 || m === 45
        },
      },
      date: '',
      time: '',
      modal: false,
      today: new Date(new Date().getTime() + 1000 * 60 * 60 * 24),
    }
  },
  computed: {
    dateRangeText() {
      return this.dates.join(' – ')
    },
  },
  methods: {
    addDates(d) {
      // $refs.dialog.save(date)
      d.push({
        date: this.date,
        time: this.time,
      })
      this.date = ''
      this.time = ''
      this.isSelectDate = true
      // this.menu = false
      this.$refs.dialog.save()
    },
    formatDate(date) {
      const d = new Date(date)
      let month = '' + (d.getMonth() + 1)
      let day = '' + d.getDate()
      const year = d.getFullYear()

      if (month.length < 2) month = '0' + month
      if (day.length < 2) day = '0' + day

      return [year, month, day].join('-')
    },
  },
}
